
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

$blue: #00539CFF;
$yellow: rgb(247, 215, 128);
$offwhite: #f8f8ff;
$text:  'Fauna One', serif;
$heading: 'Cinzel Decorative', cursive;


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  background-color: $yellow;
  
  margin: auto;
}
a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  width: 100%;
  cursor: pointer;
}

audio {
  display: none;
}

select {
  outline: none;
  cursor: pointer;
}

/* canvas Images*/
.canvas-images {
  display: none;
}

/* NAV HEADER */

.header-nav {
  color: $offwhite;
  height: 70px;
  background-color: $blue;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 2px 2px 2px lightgray;
  font-family: $text;
  

  div {
    width: 30%;
    
  }
}

.header-how-to {
  align-items: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  .how-to-link {
    cursor: pointer;
    text-align: center;
    margin: auto;
    &:hover{
      font-weight: 700;
       color: $yellow;
      transition: 0.8s;
    } 
  }
  a {
    text-align: center;
  }
  i {
    
    &:hover {
      color: $yellow;
      transition: 0.8s;
    }
  }
}

.header-title {

  .title{
    font-family: $heading;
    font-size: 50px;
    text-align: center;
  }
}

.header-about-list {

  ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  }
  

  i {
    &:hover {
      color: $yellow;
      transition: 0.8s;
    }
  }
}

/* Canvas*/

.canvas-container {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 600px;
}
.canvas {
  width: 1000px;
  height: 100%;
  border-radius: 25px;
  
  margin: 20px;
  
background-image: linear-gradient(rgb(4, 4, 65), rgb(240, 225, 142)); 

    &.won {
      background-image: linear-gradient(rgb(10, 10, 238), rgb(90, 82, 168)); 
    }
    &.lost {
      background-image: linear-gradient(rgb(1, 1, 17), rgb(44, 44, 44)); 

    }
}

/* User Form*/

.user-interaction-form {
  width: 800px;
  margin: auto;
  padding-top: 15px;
  font-family: $text;
  display: flex;
  justify-content: space-around;
  align-items:flex-start;

}
.start-button {
  padding: 10px 15px 10px 15px;
  background-color: $offwhite;
  font-family: $heading;
  font-size: 14px;
  border: 1px solid gray;
  border-radius: 20px;

  &:hover {
    background-color: $blue;
    color: $yellow;
    transition: 0.8s;
  }
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  

    &.open {
      visibility: visible;
      opacity: 1;
    }

}

.modal-background {
  position: absolute;
  background: gray;
  opacity: 0.8;
  width: 100%;
  height: 100%;
}

.modal-container {
  width: 500px;
  height: 300px;
  background-color:$blue;
  position: relative;
  padding: 30px;
  border-radius: 30px;
  color: $yellow;
  margin-top: 200px;
  border: 2px solid $yellow;
  box-shadow: 3px 3px 3px darkslategray;

  h1 {
    font-family: $heading;
    font-size: 20px;
    text-align: center;
    padding: 10px;
    font-weight: bolder;

  }

  h2 {
    font-family: $heading;
    font-size: 16px;
    text-align: center;
    padding: 15px;
    }

  p {
    font-family: $text;
    font-size: 15px;
    text-align: center;
    
  }
}

.modal-close {
  position: absolute;
  right: 10px;
  top: 10px;
  color: black;
  font-weight: bolder;
  cursor: pointer;
  border-radius: 3px;
}

/* Footer */

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-image: linear-gradient($yellow,$blue );
}

/* Volume */

#mute {
  cursor: pointer;
  width: 30px;
}

/* Cannons */

.cannon-images {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: auto;
  
}

.cannon-image {
  width: 250px;
  height: auto;
}

.flipcard {
  width: 250px;
  height: 200px;
  perspective: 1000px;
  background-color: transparent;
}

.inner {
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
  position: relative;
}

.flipcard:hover .inner {
  transform: rotateY(180deg);
}

.flip-front, .flip-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-back {
  transform: rotateY(180deg)
}

.play-instructions {
  line-height: 20px;
  
}

p.control-section-title {
  font-weight: bolder;
  font-size: 15px;
  padding-bottom: 5px;
}

.controls-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  
}
.controls {
  display: flex;
  justify-content: space-around;
  height: 75px;
  width: 90%;
}

p.key-control {
  font-size: 12px;
  line-height: 15px;
  padding-right: 10px;

  strong {
    font-size: 13px;
  }
}

.key-controls{
    display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap:wrap;
  
}


